import React from 'react';
import { Card, CardContent, Grid, Typography, Avatar } from '@material-ui/core';
import {Tooltip as Tooltips} from '@material-ui/core';
import { makeStyles } from '@material-ui/styles';
import PropTypes from 'prop-types';
import {
  LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer 
} from 'recharts';
import * as d3 from "d3";

const useStyles = makeStyles(theme => ({
  root: {
    height: '100%',
	 margin: theme.spacing(3)
  },
  small: {
    width: theme.spacing(3),
    height: theme.spacing(3),
  },
  large: {
    width: theme.spacing(7),
    height: theme.spacing(7),
  },
  content: {
    alignItems: 'center',
    display: 'flex'
  },
  title: {
    fontWeight: 700
  },
  avatar: {
    backgroundColor: theme.palette.error.main,
    height: 36,
    width: 36
  },
  icon: {
	    position: "relative",
        top: theme.spacing.unit,
        width: 32,
        height: 32
  },
  wrapIcon: {
    verticalAlign: 'middle',
    display: 'inline-flex'
   }
}));

const ThisLinePlot =props=> {
	const { thisName, data, plotLine, thisDot, avatarIcon } = props;
	const classes = useStyles();
	
    return (
	<Card>
	<CardContent>
	<Grid container  
		direction="row"
	  >
	  <Grid item>
            <Typography
              color="textSecondary"
              gutterBottom
              variant="h6"
			  className={classes.wrapIcon}
            >
			{thisName}
			{Object.keys(avatarIcon).map((showIcon, key)=>
			  <Tooltips arrow title={avatarIcon[showIcon]}>
				<Avatar id={thisName+key}>
					{showIcon} 
				</Avatar>
			  </Tooltips>
			)
			}
            </Typography>
		</Grid>
		</Grid>
<ResponsiveContainer width="100%" height={350}>
      <LineChart
        data={data}
        margin={{
          top: 5, right: 30, left: 20, bottom: 5,
        }}
      >
        <CartesianGrid strokeDasharray="3 3" />
        <XAxis dataKey="Date" />
        <YAxis />
        <Tooltip />
        <Legend />
		{Object.keys(plotLine).map((thisPlot, key)=>
		<Line id={thisPlot+key} type="monotone" dataKey={thisPlot} stroke={plotLine[thisPlot]} dot={thisDot} strokeWidth='3' />
		)
		}
      </LineChart>
  </ResponsiveContainer>
	  </CardContent>
	  </Card>
    );
}

ThisLinePlot.propTypes = {
  thisName: PropTypes.string,
  data: PropTypes.object,  
  plotLine: PropTypes.object,
  thisDot: PropTypes.bool,
  avatarIcon: PropTypes.object
};

export default ThisLinePlot