import React, { Fragment } from 'react';
import {useLocation} from "react-router";
import { Link } from "react-router-dom";
import { makeStyles } from '@material-ui/core/styles';
import { Avatar, Card, CardContent, Grid, Box, Stepper, Step, StepLabel, StepContent, Button, Paper, Typography} from '@material-ui/core';
import AddCountry from './AddCountry'
import PlotCountry from './PlotCountry'

const useStyles = makeStyles(theme => ({
  button: {
    margin: theme.spacing(1),
  },
}));

const _ = require("lodash");

const AddSecondStep=(thisCountry)=>{
	const classes = useStyles();
	const theCountry =thisCountry.thisCountry
	
	

	return (
		<Card >
		    <CardContent >
				<Grid container  justify="space-between" spacing={2} direction="row" alignItems="center" >
				<Grid item spacing={2} lg={12} sm={12} xl={6} xs={24} >
					<Typography >
						<Box textAlign="justify" m={1}>
							เลือกแสดงข้อมูลประเทศ เลือก {theCountry.toUpperCase()}
						</Box>
					</Typography>
					<Button variant="contained" color="secondary" className={	classes.button} 
						component={Link} to={{pathname: `/plotcountry`, state: {theCountry: theCountry}}}
					 >
					   {theCountry}
					</Button>
					
					<Button variant="contained" color="secondary" className={classes.button} 
						component={Link} to={{pathname: `/comparethai`, state: {theCountry: theCountry}}}
					>
						VS Thailand
					</Button>
					<Typography >
						<Box textAlign="justify" m={1}>
							เปรียบเทียบ {theCountry.toUpperCase()} กับประเทศไทย เลือก THAILAND
						</Box>
					</Typography>
			</Grid>
			</Grid>
		</CardContent>
	</Card>
	)
}

const getSteps=(input, interventionName)=> {
	let step1=input.slug?('Intervention: '+input.Country+' ID: '+input.Slug) : 'เลือกประเทศที่ต้องการข้อมูล'+':  '+interventionName
	let step2=input.email?'Workforce: '+input.email+' ID: '+input.id :  'เลือกแสดงข้อมูลเฉพาะ '+interventionName+'  หรือเปรียบเทียบกับไทย'
  return [step1, step2];
}

const getStepContent=(step, input, setInput, interventionName, setInterventionName, url)=> {
	let thisIntervention=input.Slug?interventionName:''
  switch (step) {
    case 0:
      return <Fragment>
					<AddCountry input={input} setInput={setInput} url={url} thisFieldName={'Slug'}  />
				</Fragment>;
    case 1:
      return <Fragment>
					<AddSecondStep thisCountry={input.Slug} />
				</Fragment>;
    default:
      return 'Unknown step';
  }
}

const CompareCountry=()=> {
  const [activeStep, setActiveStep] = React.useState(0);
  const [input, setInput] = React.useState('')
  const [countryName, setCountryName] = React.useState('')
  const [compareName, setCompareName] = React.useState('')
  const steps = getSteps(input, countryName);
  const url= 'https://api.covid19api.com/countries'
  
  const handleNext = () => {
	setCountryName(input.Slug?input.Slug:countryName)
    setActiveStep(prevActiveStep => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep(prevActiveStep => prevActiveStep - 1);
  };

  const handleReset = () => {
    setActiveStep(0);
  };

  return (
    <div>
      <Stepper activeStep={activeStep} orientation="vertical">
        {steps.map((label, index) => (
          <Step key={label}>
            <StepLabel>{label}</StepLabel>
            <StepContent>
              <Typography variant='h3'>{getStepContent(index, input, setInput, countryName, setCountryName, url)}</Typography>
              <div>
			  		    <Box textAlign="justify" m={2}/>
                <div>
                  <Button
                    disabled={activeStep === 0}
                    onClick={handleBack}
                  >
                    Back
                  </Button>
                  <Button
                    variant="contained"
                    color="primary"
                    onClick={handleNext}
                  >
                    {activeStep === steps.length - 1 ? 'Finish' : 'Next'}
                  </Button>
                </div>
              </div>
            </StepContent>
          </Step>
        ))}
      </Stepper>
 

    </div>
  );
}


export default CompareCountry