import React, {Fragment} from 'react';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/styles';
import { Card, CardContent, Grid, Typography, Avatar, Tooltip } from '@material-ui/core';
import ArrowDownwardIcon from '@material-ui/icons/ArrowDownward';
import ArrowUpwardIcon from '@material-ui/icons/ArrowUpward';
import MoneyIcon from '@material-ui/icons/Money';
import ThaiFlag from "img/thaiFlagS.png";

const useStyles = makeStyles(theme => ({
  root: {
    height: '100%',
	 margin: theme.spacing(1)
  },
  small: {
    width: theme.spacing(3),
    height: theme.spacing(3),
  },
  large: {
    width: theme.spacing(7),
    height: theme.spacing(7),
  },
  content: {
    alignItems: 'center',
    display: 'flex'
  },
  title: {
    fontWeight: 700
  },
  avatar: {
    backgroundColor: theme.palette.error.main,
    height: 36,
    width: 36
  },
  icon: {
	    position: "relative",
        top: theme.spacing.unit,
        width: 32,
        height: 32
  },
  difference: {
    marginTop: theme.spacing(2),
    display: 'flex',
    alignItems: 'center'
  },
  differenceIcon: {
    color: theme.palette.error.dark
  },
  differenceValue: {
    color: theme.palette.error.dark,
    marginRight: theme.spacing(1)
  },
  wrapIcon: {
    verticalAlign: 'middle',
    display: 'inline-flex'
   }
}));

const Budget = props => {
  const { className, thisName, thisAmount, globalAmount, avatarIcon, thisTooltip, ...rest } = props;
  const classes = useStyles();

  return (

    <Card
      {...rest}
      className={clsx(classes.root, className)}
    >
	<Tooltip arrow title={thisTooltip}>
      <CardContent>
	  <Grid container  
		direction="row"
	  >
	  <Grid item>
            <Typography
              className={classes.wrapIcon}
              color="textSecondary"
              gutterBottom
              variant="h4"
			  verticalalign='middle'
			  display='inline-flex'
            >
			{thisName} &nbsp;&nbsp;&nbsp;&nbsp;
            <Avatar className={classes.avatar}>
				{avatarIcon} 
            </Avatar>			
            </Typography>
		</Grid>
		</Grid>
	  <Grid container  
		direction="row"
	  >
		<Grid item>
			<Typography variant="h3" className={classes.wrapIcon}>
				<Avatar className={classes.small} alt="ThaiLand" src={ThaiFlag} />
					&nbsp;  :  {thisAmount}  cases  &nbsp;&nbsp;&nbsp;&nbsp;
			{ globalAmount>0 &&
				 <Fragment>
					<Avatar className={classes.small} >
						{'\u{1f30f}'} 
					</Avatar>
						&nbsp;   : {globalAmount} cases 
				</Fragment>
			}
			</Typography>

		</Grid>
		</Grid>
	 
	 <div className={classes.difference}>
		 {thisAmount>0 &&
          <ArrowUpwardIcon className={classes.differenceIcon} />
		 }
		 {thisAmount<0 &&
		  <ArrowDownwardIcon className={classes.differenceIcon} />	 
		 }
          <Typography
            className={classes.differenceValue}
            variant="body2"
          >
			{avatarIcon} 
          </Typography>
          <Typography
            className={classes.caption}
            variant="caption"
          >
            Since yesterday
          </Typography>
        </div>
      </CardContent>
	  </Tooltip>
    </Card>

  );
};

Budget.propTypes = {
  className: PropTypes.object,  
  thisName: PropTypes.string,
  thisAmount: PropTypes.string,
  globalAmount: PropTypes.number,
  avatarIcon: PropTypes.string,
  thisTooltip: PropTypes.string
};

export default Budget;
