import React from 'react';
import * as d3 from "d3";

const AdjustData=(theArray, field1, field2)=> {
	const countryData=theArray
	const firstField=field1
	const secondField=field2
	const  totalAllByDate = d3.nest()
	  .key(function(d) { return d.Date})
	  .key(function(d) { return d.CountryCode})
	  .rollup(function(v) { return d3.sum(v, function(d) { return d.Cases; })
   }).object(countryData);
	
	const refDate = [{'Confirmed':0},{'Accumulated':0}]
	const choosePlot =totalAllByDate?Object.keys(totalAllByDate).map((caseConfirmDate, key)=>{
		let chooseCountry=Object.keys(totalAllByDate[caseConfirmDate])[0]
		let upToDateCases=totalAllByDate[caseConfirmDate][chooseCountry]
		let newCases=upToDateCases - refDate[0]['Confirmed']
		 refDate[0]['Confirmed']=upToDateCases	
		return  newCases>=0 && {"Date": caseConfirmDate.substr(0, 10).replace(/-/g,'/'), 
			[firstField]:upToDateCases, 
			[secondField]:newCases,
	}}):[]
	return choosePlot
}
export default AdjustData
