import React from "react";
import clsx from "clsx";
import { Link } from "react-router-dom";

import { Drawer, List, ListItem, ListItemText, Box, Grid, Avatar, Tooltip} from "@material-ui/core"; 

import { green } from '@material-ui/core/colors';
import { red } from '@material-ui/core/colors';
import { blue } from '@material-ui/core/colors';
import { yellow } from '@material-ui/core/colors';

import CompareIcon from '@material-ui/icons/Compare';
import DashboardIcon from '@material-ui/icons/Dashboard';
import TimelineIcon from '@material-ui/icons/Timeline';

import ListItemIcon from '@material-ui/core/ListItemIcon';
import PeopleIcon from '@material-ui/icons/People';
import BarChartIcon from '@material-ui/icons/BarChart';
import AssignmentIcon from '@material-ui/icons/Assignment';
import HomeIcon from '@material-ui/icons/Home';
import VpnKeyOutlinedIcon from '@material-ui/icons/VpnKeyOutlined';
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import {useStyles} from './useStyles';


const MyDrawer = ({ title, variant, open, onClose, onItemClick })=>{
	  const classes = useStyles()
	  return (
      <Drawer
        variant={variant}
        open={open}
        onClose={onClose}
        classes={{
          paper: classes.drawerPaper
        }}
      >
	  		<Box mt={2} />
		<Grid
			container
			spacing={1}
			justify="center"
			alignItems="center"
		>
		  <Avatar alt='COVID' className={classes.avatar} >
			  <VpnKeyOutlinedIcon color="primary" />
		  </Avatar>
		  <Avatar alt='COVID' className={classes.avatar} >
			  {title}
		  </Avatar>
		  <Avatar className={classes.orange}>{title.slice(0, 1)}</Avatar>
		</Grid> 
		<Box mt={2} />
        <div
          className={clsx({
            [classes.toolbarMargin]: variant === "persistent"
          })}
        />
        <List>
		  <Tooltip arrow title='ทำความรู้จักกับ COVID-19 เล็กน้อย ใช้เวลาไม่เกิน 2 นาที โดยชี้ที่รูปแล้วอ่านคำอธิบายที่ปรากฎขึ้นมา'>
          <ListItem
            button
            component={Link}
            to="/"
            onClick={onItemClick("Must know COVID-19")}
          >

		  <ListItemIcon>
			<HomeIcon style={{ color: red[500] }}/>
		  </ListItemIcon>
            <ListItemText>หน้าแรก</ListItemText>
		  
          </ListItem>
		  </Tooltip>
		  <Tooltip arrow title='รู้เท่าทัน สถานการณ์ปัจจุบัน เปรียบเทียบไทยกับทั่วโลก และเขตสุขภาพที่ 8'>
		  <ListItem
            button
            component={Link}
            to="/dashboard"
            onClick={onItemClick("COVID-19 Updated")}
          >
			  <ListItemIcon>
				<DashboardIcon color="secondary"/>
			  </ListItemIcon>
            <ListItemText>สถานการณ์ปัจจุบัน</ListItemText>
          </ListItem>
		  </Tooltip>
		  <Tooltip arrow title='ข้อมูลของประเทศไทยเมื่อนำมาวิเคราะห์จะบ่งบอก แนวโน้ม ทิศทาง และบ่งชี้ว่าอะไรกำลังจะเกิดขึ้น เพื่อการเตรียมพร้อมอย่างเหมาะสม '>
		  <ListItem
            button
            component={Link}
            to="/lineplot"
            onClick={onItemClick("Trend and Information")}
          >
			  <ListItemIcon>
				<TimelineIcon color="secondary"/>
			  </ListItemIcon>

            <ListItemText>แนวโน้ม และข้อมูล</ListItemText>
          </ListItem>
		  </Tooltip>
		  <Tooltip arrow title='เมื่อเปรียบเทียบข้อมูลกันระหว่างประเทศต่าง ๆ กับไทย จะบ่งบอก ความปกติ ผิดปกติ ต่างกันหรือตรงกัน บ่งชี้ว่านโยบายและการดำเนินการแต่ละแห่งเหมาะสมหรือไม่'>
		  <ListItem
            button
            component={Link}
            to="/comparecountry"
            onClick={onItemClick("Compare with others")}
          >
			  <ListItemIcon>
				<CompareIcon color="primary"/>
			  </ListItemIcon>
            <ListItemText>เทียบกับประเทศอื่น</ListItemText>
          </ListItem>
		  </Tooltip>


		 </List>

	</Drawer>
  )
};

export default MyDrawer
