import React from 'react';
import { makeStyles } from '@material-ui/styles';
import { Grid, Card, CardContent, Typography, Avatar } from '@material-ui/core';
import ArrowDownwardIcon from '@material-ui/icons/ArrowDownward';
import GetCount from 'crud/GetCount';

import {
  Budget
} from './components';

const _ = require("lodash");

const useStyles = makeStyles(theme => ({
  root: {
    padding: theme.spacing(2)
  }
}));

const Dashboard = () => {
  const classes = useStyles();
  const baseUrl =  'https://covid19.th-stat.com/api/open/today'
  const worldUrl = "https://api.covid19api.com/summary"
  const caseSumUrl = "https://covid19.th-stat.com/api/open/cases/sum"  
  const data = GetCount(baseUrl)
  const confirm = data.Confirmed
  const recover = data.Recovered
  const hospitalize = data.Hospitalized
  const totalDeath = data.Deaths
  const newCase = data.NewConfirmed
  const newRecover = data.NewRecovered
  const newAdmit = data.NewHospitalized
  const newDeath = data.NewDeaths
  const updateOn = data.UpdateDate

  const worldData = GetCount(worldUrl)
  const global= worldData.Global
  const caseSum = GetCount(caseSumUrl)

  const ud= _.get(caseSum, "Province['Udon Thani']")
  const nb= _.get(caseSum, "Province['Nong Bua Lamphu']")
  const nk= _.get(caseSum, "Province['Nong Khai']")
  const lo= _.get(caseSum, "Province['Loei']")
  const np= _.get(caseSum, "Province['Nakhon Phanom']")
  const sk= _.get(caseSum, "Province['Sakon Nakhon']")
  const bk= _.get(caseSum, "Province['Bueng Kan']")
const r8=ud
  
  return (
    <div className={classes.root}>
		<Grid container spacing={2}   direction="row"  justify="center" alignItems="center">
		<Grid item xs={12} sm={6}>
          <Budget	thisName={'Until Now Confirmed Cases'} thisAmount={confirm} globalAmount = {_.get(global, 'TotalConfirmed')} avatarIcon={' \u{1f631}'} thisTooltip={'Until Now Confirmed Cases: จำนวนผู้ติดเชื้อ สะสม เปรียบเทียบประเทศไทย กับ ทั่วโลก บ่งชี้ถึงสถานการณ์ปัจจุบัน'}/>
		</Grid>
		<Grid item  xs={12} sm={6}>
		  <Budget	thisName={'Today New Confirmed Cases'} thisAmount={newCase} globalAmount={_.get(global, 'NewConfirmed') }  avatarIcon={' \u{1f631}'} thisTooltip={'Today New Confirmed Cases: จำนวนผู้ติดเชื้อ ที่เพิ่มขึ้นในวันนี้  เปรียบเทียบประเทศไทย กับ ทั่วโลก บ่งชี้ถึงสถานการณ์ปัจจุบันกำลังเพิ่มขึ้นลดลงอย่างไร'}/>
		 </Grid>
		<Grid item xs={12} sm={6}>
		  <Budget	thisName={'Until Now Recovered Cases'} thisAmount={recover}  globalAmount = {_.get(global, 'TotalRecovered')} avatarIcon={' \u{1f917}'} thisTooltip={'Until Now Recovered Cases: จำนวนผู้ป่วยหายจากโรคนี้แล้ว สะสม  เปรียบเทียบประเทศไทย กับ ทั่วโลก บ่งชี้ถึงสถานการณ์ปัจจุบัน'}/>
		</Grid>
		<Grid item  xs={12} sm={6}>
		  <Budget	thisName={'Today New Recovered Cases '} thisAmount={newRecover} globalAmount={_.get(global, 'NewRecovered')} avatarIcon={' \u{1f917}'} thisTooltip={'Today New Recovered Cases: จำนวนผู้ป่วยหายจากโรคในวันนี้  เปรียบเทียบประเทศไทย กับ ทั่วโลก บ่งชี้ถึงสถานการณ์ปัจจุบัน'}/>
		 </Grid>
		<Grid item  xs={12} sm={6}>
		  <Budget	thisName={'Until Now Mortality '} thisAmount={totalDeath} globalAmount={_.get(global, 'TotalDeaths')} avatarIcon={' \u{1f480}'} thisTooltip={'Until Now  Mortality: จำนวนผู้ป่วยเสียชีวิตด้วยโรคนี้  เปรียบเทียบประเทศไทย กับ ทั่วโลก บ่งชี้ถึงสถานการณ์ปัจจุบัน'}/>
		</Grid>
		<Grid item xs={12} sm={6}>
			<Budget thisName={'Today New Mortality '} thisAmount={newDeath}  globalAmount ={_.get(global,'NewDeaths') } avatarIcon={' \u{1f47b}'} thisTooltip={'Today New Mortality: จำนวนผู้ป่วยเสียชีวิตด้วยโรคนี้ ในวันนี้  เปรียบเทียบประเทศไทย กับ ทั่วโลก บ่งชี้ถึงสถานการณ์ปัจจุบัน'}/>
		 </Grid>
		 <Grid item  xs={12} sm={6}>
		  <Budget	thisName={'Until now Still Admission in Thailand'} thisAmount={hospitalize} avatarIcon={' \u{1f6cf}'} thisTooltip={'Until Now  Still Admited in Thailand: จำนวนผู้ป่วยโรคนี้รับไว้ในโรงพยาบาล สะสม ของประเทศไทย บ่งชี้ถึงการใช้ทรัพยากรสาธารณสุข'}/>
		</Grid>
		<Grid item  xs={12} sm={6}>
			<Budget	thisName={'Today New Admission in Thailand'} thisAmount={newAdmit} avatarIcon={' \u{1f3e5}'} thisTooltip={'Today New Admission in Thailand: จำนวนผู้ป่วยโรคนี้รับไว้ในโรงพยาบาลวันนี้  ของประเทศไทย บ่งชี้ถึงความต้องการทรัพยากรสาธารณสุขในปัจจุบัน'}/>
		 </Grid>
		 <Grid item  xs={12} sm={6}>
			<Budget	thisName={'Until now Confirmed Cases at 8th Region'} thisAmount={ud+sk+nb+nk+np+lo+(bk?bk:0)} avatarIcon={' \u{1f631}'} thisTooltip={'Until now Confirmed Cases at 8th Region: จำนวนผู้ติดเชื้อในเขตสุขภาพที่ 8 รวม 7 จังหวัด'}/>
		 </Grid>
		 <Grid item  xs={12} sm={6}>
			<Budget	thisName={'Until now Confirmed Cases at Udon Thani'} thisAmount={ud} avatarIcon={' \u{1f631}'} thisTooltip={'Until now Confirmed Cases at Udon Thani: จำนวนผู้ติดเชื้อ สะสม ในจังหวัดอุดรธานี'}/>
		 </Grid>
		 <Grid item  xs={12} sm={6}>
			<Budget	thisName={'Until now Confirmed Cases at Nong Bua Lamphu'} thisAmount={nb} avatarIcon={' \u{1f631}'} thisTooltip={'Until now Confirmed Cases at Nong Bua Lamphu: จำนวนผู้ติดเชื้อในจังหวัดหนองบัวลำภู'}/>
		 </Grid>
		 <Grid item  xs={12} sm={6}>
			<Budget	thisName={'Until now Confirmed Cases at Nong Khai'} thisAmount={nk} avatarIcon={' \u{1f631}'} thisTooltip={'Until now Confirmed Cases at Nong Khai: จำนวนผู้ติดเชื้อในจังหวัดหนองคาย'}/>
		 </Grid>
		 <Grid item  xs={12} sm={6}>
			<Budget	thisName={'Until now Confirmed Cases at Loei'} thisAmount={lo} avatarIcon={' \u{1f631}'} thisTooltip={'Until now Confirmed Cases at Loei: จำนวนผู้ติดเชื้อในจังหวัดเลย'}/>
		 </Grid>
		 <Grid item  xs={12} sm={6}>
			<Budget	thisName={'Until now Confirmed Cases at Nakhon Phanom'} thisAmount={np} avatarIcon={' \u{1f631}'} thisTooltip={'Until now Confirmed Cases at Nakhon Phanom: จำนวนผู้ติดเชื้อในจังหวัดนครพนม'}/>
		 </Grid>
		 <Grid item  xs={12} sm={6}>
			<Budget	thisName={'Until now Confirmed Cases at Sakon Nakhon'} thisAmount={sk} avatarIcon={' \u{1f631}'} thisTooltip={'Until now Confirmed Cases at Sakon Nakhon: จำนวนผู้ติดเชื้อในจังหวัดสกลนคร'}/>
		 </Grid>
		 <Grid item  xs={12} sm={6}>
			<Budget	thisName={'Until now Confirmed Cases at Bueng Kan'} thisAmount={bk?bk:0} avatarIcon={' \u{1f631}'} thisTooltip={'Until now Confirmed Cases at Bueng Kan: จำนวนผู้ติดเชื้อในจังหวัดบึงกาฬ'}/>
		 </Grid>
      </Grid>

		<Grid container spacing={2}   direction="row"  justify="center" alignItems="center">
		<Grid item >
			<Typography variant="h6"> covid19.th-stat.com  Update at {updateOn}</Typography>
	  </Grid>
	  </Grid>
	  
    </div>
  );
};

export default Dashboard;
