import React, {Fragment, useState} from 'react'
import { useLocation} from "react-router";
import { makeStyles } from '@material-ui/styles'
import { Grid, Card, CardContent, Typography, Avatar, Tooltip } from '@material-ui/core'
import * as d3 from "d3";
import axios from 'axios'
import GetCount from 'crud/GetCount'
import AdjustData from './AdjustData'
import {
  ThisLinePlot
} from './components';

const _ = require("lodash");

const useStyles = makeStyles(theme => ({
  root: {
    padding: theme.spacing(2)
  }
}));

const PlotCountry = () => {
	let location = useLocation();

	const selectCountry=location.state.theCountry
	const classes = useStyles()
	
	const selectUrl= 'https://api.covid19api.com/dayone/country/'+selectCountry+'/status/confirmed'
	const recoverUrl= 'https://api.covid19api.com/dayone/country/'+selectCountry+'/status/recovered'
	const deathUrl= 'https://api.covid19api.com/dayone/country/'+selectCountry+'/status/deaths'
	const countryData = GetCount(selectUrl)	
	const confirmGetPlot = AdjustData(countryData,'Confirmed', 'NewConfirmed')	
	const recoverData = GetCount(recoverUrl)
	const recoverGetPlot = AdjustData(recoverData,'Recovered', 'NewRecovered')	
	const deathData = GetCount(deathUrl)
	const deathGetPlot = AdjustData(deathData, 'Deaths', 'NewDeaths')	
	
	const allDataTogether =confirmGetPlot?Object.keys(confirmGetPlot).map((caseConfirmDate, key)=>{
		const caseConfirm = confirmGetPlot[caseConfirmDate].Confirmed
		const caseNewConfirm = confirmGetPlot[caseConfirmDate].NewConfirmed
		const caseRecover = recoverGetPlot[key]?recoverGetPlot[key].Recovered:0
		const caseNewRecover = recoverGetPlot[key]?recoverGetPlot[key].NewRecovered:0
		const caseDeaths = deathGetPlot[key]?deathGetPlot[key].Deaths:0
		const caseNewDeaths = deathGetPlot[key]?deathGetPlot[key].NewDeaths:0

		return  confirmGetPlot[caseConfirmDate].Date && {"Date": confirmGetPlot[caseConfirmDate].Date, 
			"Confirmed": caseConfirm, 
			"NewConfirmed": caseNewConfirm,
			"Recovered": caseRecover,
			"NewRecovered": caseNewRecover,
			"Deaths": caseDeaths,
			"NewDeaths": caseNewDeaths, 
			"recoverRate": caseRecover*100/(caseRecover+caseDeaths),
			"deathRate": caseDeaths*100/(caseDeaths+caseRecover)
	}
	}):[]
	
	
	return (
    <div className={classes.root}>
		<Grid container spacing={2}   direction="row"  justify="center" alignItems="center">
		<Grid item xs={12} sm={6}>
          <ThisLinePlot	
		    id='chart1'
			thisName={selectCountry.toUpperCase()+'  Trend of Confirmed, Recovered, Deaths Cases'} 
			data={allDataTogether} 
		    plotLine={{"Confirmed":"#8884d8","Recovered": "#82ca9d", "Deaths":"#50556C"}} 
			thisDot={false}
			avatarIcon={{' \u{1f631}':'Confirmed : จำนวนป่วยด้วยโรค COVID-19 สะสม บ่งชี้ถึงแนวโน้มการป่วย',' \u{1f917}':'Recovered : จำนวนหายจากโรค สะสม บ่งชี้ถึงผลลัพธ์การรักษา',' \u{1f480}':'Deaths : จำนวนเสียชีวิตจากโรค COVID-19 สะสม'}}
		/>
		</Grid>
		<Grid item xs={12} sm={6}>
          <ThisLinePlot	
		    id='chart2'
			thisName={selectCountry.toUpperCase()+' New Confirmed, New Recovered and New Deaths Cases  '} 
			data={allDataTogether} 
		    plotLine={{'NewConfirmed':'#8884d8','NewRecovered':'#82ca9d', "NewDeaths":'#444444'}} 
			avatarIcon={{' \u{1f631}':'NewConfirmed : จำนวนป่วยด้วยโรค COVID-19 รายใหม่วันนี้ บ่งชี้ถึงปัญหาปัจจุบัน', ' \u{1f917}':'NewRecovered : จำนวนหายจากโรควันนี้ บ่งชี้ถึงผู้เคยติดเชื้อได้กลับสู่ชุมชน ', ' \u{1f47b}':'NewDeaths : จำนวนเสียชีวิตจากโรค COVID-19 วันนี้' }}
			  thisDot={false}
		/>
		</Grid>
		<Grid item xs={12} sm={6}>
          <ThisLinePlot	
		    id='chart3'
			thisName={selectCountry.toUpperCase()+'  Trend of Recovered, New Recovered Cases  '} 
			data={recoverGetPlot} 
		    plotLine={{"Recovered":"#8884d8","NewRecovered": "#82ca9d" }} 
			thisDot={false}
			avatarIcon={{ ' \u{1f917}':'Recovered : จำนวนหายจากโรค สะสม บ่งชี้ถึงผลลัพธ์การรักษา',  ' \u{1f917}':'NewRecovered : จำนวนหายจากโรควันนี้ บ่งชี้ถึงผู้เคยติดเชื้อได้กลับสู่ชุมชน '}}
		/>
		</Grid>
		<Grid item xs={12} sm={6}>
          <ThisLinePlot	
		    id='chart4'
			thisName={selectCountry.toUpperCase()+'  Trend of Deaths, New Deaths Cases  '} 
			data={deathGetPlot} 
		    plotLine={{"Deaths":"#8884d8","NewDeaths": "#82ca9d" }} 
			thisDot={false}
			avatarIcon={{' \u{1f480}':'Deaths : จำนวนเสียชีวิตจากโรค COVID-19 สะสม', ' \u{1f47b}':'NewDeaths : จำนวนเสียชีวิตจากโรค COVID-19 วันนี้'}}
		/>
		</Grid>
		<Grid item xs={12} sm={6}>
		<ThisLinePlot	
		    id='chart5'
			thisName={'Outcome from Mortality and Recovery Rate'} 
			data={allDataTogether} 
		    plotLine={{"recoverRate": "#8884d8", 'deathRate':'#82ca9d'}} 
			thisDot={false}
			avatarIcon={{'\u{1f917}':'recoverRate: อัตราการหายป่วยจากโรค COVID-19', ' \u{1f480}':'deathRate: อัตราการเสียชีวิตจากโรค COVID-19'}}/>
		</Grid>
		</Grid>

	</div>
	)
	
}

export default PlotCountry