import React from "react";
import { Route, Redirect, Switch } from "react-router-dom";
import { withStyles } from "@material-ui/core/styles";
import Home from "../pages/Home";
import Dashboard from "pages/Dashboard";
import LinePlot from "pages/LinePlot";
import CompareCountry from "pages/CompareCountry";
import PlotCountry from "pages/CompareCountry/PlotCountry";
import CompareThai from "pages/CompareCountry/CompareThai";

const MyMain = 
  () => 
      <main >
	  <Switch>
		  <Route exact path="/">
			<Home/>
		  </Route>
		  <Route path="/dashboard"> 
			<Dashboard/>
		   </Route>
		  <Route path="/lineplot"> 
			<LinePlot/>
		   </Route>
		   <Route path="/comparecountry"> 
			<CompareCountry/>
		   </Route>
		   <Route path="/plotcountry"> 
			<PlotCountry/>
		   </Route>
		   <Route path="/comparethai"> 
			<CompareThai/>
		   </Route>
		   <Route>
				<Home/>
			</Route>
		</Switch>
      </main>

export default MyMain
