import React from 'react';
import PropTypes from 'prop-types';
import { Grid, TextField, Card, CardContent, CircularProgress, Typography} from '@material-ui/core';
import Autocomplete from '@material-ui/lab/Autocomplete';
import axios from 'axios';


function sleep(delay = 10) {
  return new Promise(resolve => {
    setTimeout(resolve, delay);
  });
}

const AddCountry=(props)=>{
  const { input, setInput, url, thisFieldName} = props
  const thisLabel = 'Choose Country'
  const [open, setOpen] = React.useState(false);
  const [options, setOptions] = React.useState([]);
  const loading = open && options.length === 0;
  

  React.useEffect(() => {
    let active = true;

    if (!loading) {
      return undefined;
    }

    (async () => {
	  const configAxios={url: url, method: 'get'
		  } 		
      const response = await axios(configAxios);
      await sleep(1e3); // For demo purposes.
      const countries=response.data;

      if (active) {
        setOptions(Object.keys(countries).map(key => countries[key]));
      }
    })();

    return () => {
      active = false;
    };
  }, [loading]);

  React.useEffect(() => {
    if (!open) {
      setOptions([]);
    }
  }, [open]);

  const countryToFlag=(Slug)=> {
  return Slug;
}

  return (

  
    <Autocomplete
      id="formSelect"
      style={{ width: 300 }}
      open={open}
      onOpen={() => {
        setOpen(true);
      }}
      onClose={() => {
        setOpen(false);
      }}
      getOptionSelected={(option, value) => option[thisFieldName] === value[thisFieldName]}
      getOptionLabel={option => option[thisFieldName]}
      options={options}
      loading={loading}
	  disableClearable 
      onChange={(e,v) => setInput(v)}
      renderInput={params => (
        <TextField
          {...params}
          label={input[thisFieldName]?input[thisFieldName]:thisLabel}
		  onChange={({ target }) => setInput(target.value)}
          variant="outlined"
          InputProps={{
            ...params.InputProps,
            endAdornment: (
              <React.Fragment>
                {loading ? <CircularProgress color="inherit" size={20} /> : null}
                {params.InputProps.endAdornment}
              </React.Fragment>
            ),
          }}
        />
      )}
    />

  );
}

AddCountry.propTypes = {
  input: PropTypes.object.isRequired,
  setInput: PropTypes.func.isRequired,
  url: PropTypes.string.isRequired,
  thisFieldName: PropTypes.string.isRequired,
};


export default AddCountry