import React from 'react';
import { makeStyles } from '@material-ui/styles';
import { Grid, Card, CardContent, Typography, Avatar, Tooltip } from '@material-ui/core';

import GetCount from 'crud/GetCount';
import * as d3 from "d3";
import {
  ThisLinePlot
} from './components';

const _ = require("lodash");

function compareValues(key, order = 'asc') {
  return function innerSort(a, b) {
    if (!a.hasOwnProperty(key) || !b.hasOwnProperty(key)) {
      // property doesn't exist on either object
      return 0;
    }
    const varA = (typeof a[key] === 'string')
      ? a[key].toUpperCase() : a[key];
    const varB = (typeof b[key] === 'string')
      ? b[key].toUpperCase() : b[key];

    let comparison = 0;
    if (varA > varB) {
      comparison = 1;
    } else if (varA < varB) {
      comparison = -1;
    }
    return (
      (order === 'desc') ? (comparison * -1) : comparison
    );
  };
}


const useStyles = makeStyles(theme => ({
  root: {
    padding: theme.spacing(2)
  }
}));

const LinePlot = () => {
	const classes = useStyles();
  	const timeUrl =  'https://covid19.th-stat.com/api/open/timeline'
	const timeData = GetCount(timeUrl)
	const data = timeData.Data  
	const updateOn = timeData.UpdateDate
	const getOutcome=()=>{
	    let thisOutcome=timeData.Data?timeData.Data:[]
		thisOutcome.map((row, key)=>{
			thisOutcome[key].recoverRate= row.Recovered*100/(row.Recovered+row.Deaths)
			thisOutcome[key].deathRate= row.Deaths*100/(row.Recovered+row.Deaths)
			}
		)
	return (thisOutcome)
  }

  const casesData = GetCount( 'https://covid19.th-stat.com/api/open/cases')
  const allData = casesData.Data
  
  const ud = allData?allData.filter((findAt)=>{
  return findAt.Province==="อุดรธานี"}):[]
  const nb = allData?allData.filter((findAt)=>{
  return findAt.Province==="หนองบัวลำภู"}):[]
  const np = allData?allData.filter((findAt)=>{
  return findAt.Province==="นครพนม"}):[]
  const lo = allData?allData.filter((findAt)=>{
  return findAt.Province==="เลย"}):[]
  const nk = allData?allData.filter((findAt)=>{
  return findAt.Province==="หนองคาย"}):[]
  const sk = allData?allData.filter((findAt)=>{
  return findAt.Province==="สกลนคร"}):[]
  const bk = allData?allData.filter((findAt)=>{
  return findAt.Province==="บึงกาฬ"}):[]
  const r8 = [...ud, ...nb, ...nk, ...lo, ...np, ...sk, ...bk]
  const r8Sort = r8.sort(compareValues('ConfirmDate', 'asc'))
  const allSort = allData?allData.sort(compareValues('ConfirmDate', 'asc')):[]
  
  const allCount  = allSort.map( (row, key) => {
	  row.caseCount=1
	  return row
  })
  
  const r8Count = r8Sort.map( (row, key) => {
	  row.caseCount=1
	  row.accumulate=key+1
	  return row
  })
  
  const  caseSum = r8Count.reduce(function(total, d) {
  return total + d.caseCount
	}, 0)

  const  totalAllByDate = d3.nest()
  .key(function(d) { return d.ConfirmDate})
  .key(function(d) { return d.ProvinceEn})
  .rollup(function(v) { return d3.sum(v, function(d) { return d.caseCount; })
   })
  .object(allCount);
  
  const  totalByDate = d3.nest()
  .key(function(d) { return d.ConfirmDate})
  .rollup(function(v) { return {"totalCount": d3.sum(v, function(d) { return d.caseCount; }),
		"accumulate": d3.max(v, function(d) { return d.accumulate; })  
  } })
  .object(r8Count);

 const r8Ok=[{"UT":0},{"NB":0},{"LO":0},{"SK":0},{"NP":0},{"NK":0},{"BK":0},{"r8Accumulate":0}]
 const totalR8ByDate = 
	  Object.keys(totalAllByDate).map((thisConfirmDate, key)=>{
		  let r8array=[{"Udon Thani":0},{"Nong Bua Lamphu":0},{"Loei":0},{"Sakon Nakhon":0},{"Nakhon Phanom":0},{"Nong Khai":0},{"Bueng Kan":0},{"r8Daily":0}]
		  let prov=Object.keys(totalAllByDate[thisConfirmDate]).map((province, key)=>{
			switch (province) {
				case "Udon Thani" :
					r8array[0]={"Udon Thani":totalAllByDate[thisConfirmDate][province]}
					break
				case "Nong Bua Lamphu" :
					r8array[1]={"Nong Bua Lamphu":totalAllByDate[thisConfirmDate][province]}
					break
				case "Loei":
				    r8array[2]={"Loei": totalAllByDate[thisConfirmDate][province]}
					break
				case "Sakon Nakhon":
					r8array[3]={"Sakon Nakhon":totalAllByDate[thisConfirmDate][province]}
					break
				case "Nakhon Phanom":
				    r8array[4]= {"Nakhon Phanom":totalAllByDate[thisConfirmDate][province]}
					break
				case "Nong Khai":
					r8array[5]= {"Nong Khai":totalAllByDate[thisConfirmDate][province]}
					break
				case "Bueng Kan":
					r8array[6]={"Bueng Kan":totalAllByDate[thisConfirmDate][province]}
					break		
					return  
			  }
		})
        let sumDaily=r8array[0]["Udon Thani"]+r8array[1]["Nong Bua Lamphu"]+r8array[2]["Loei"]+r8array[3]["Sakon Nakhon"]+r8array[4]["Nakhon Phanom"]+r8array[5]["Nong Khai"]+r8array[6]["Bueng Kan"]
		let accCases=sumDaily+r8Ok[7]["r8Accumulate"]
		let udAcc=r8array[0]["Udon Thani"]+r8Ok[0]["UT"]
		let nbAcc=r8array[1]["Nong Bua Lamphu"]+r8Ok[1]["NB"]
		let loAcc=r8array[2]["Loei"]+r8Ok[2]["LO"]
		let skAcc=r8array[3]["Sakon Nakhon"]+r8Ok[3]["SK"]
		let npAcc=r8array[4]["Nakhon Phanom"]+r8Ok[4]["NP"]
		let nkAcc=r8array[5]["Nong Khai"]+r8Ok[5]["NK"]
		let bkAcc=r8array[6]["Bueng Kan"]+r8Ok[6]["BK"]
		r8array[7]={"r8Daily":sumDaily}
		r8Ok[7]={"r8Accumulate":accCases}
		r8Ok[0]={"UT":udAcc}
		r8Ok[1]={"NB":nbAcc}
		r8Ok[2]={"LO":nbAcc}
		r8Ok[3]={"SK":skAcc}
		r8Ok[4]={"NP":npAcc}
		r8Ok[5]={"NK":nkAcc}
		r8Ok[6]={"BK":bkAcc}
		
		return {"Date":thisConfirmDate.substr(0, 10).replace(/-/g,'/') , 
					"Udon Thani": r8array[0]["Udon Thani"], 
					"Nong Bua Lamphu": r8array[1]["Nong Bua Lamphu"],
					"Loei": r8array[2]["Loei"],
					"Sakon Nakhon": r8array[3]["Sakon Nakhon"],
					"Nakhon Phanom": r8array[4]["Nakhon Phanom"],
					"Nong Khai": r8array[5]["Nong Khai"],
					"Bueng Kan": r8array[6]["Bueng Kan"],
					"UT":udAcc,
					"NB":nbAcc,
					"LO":loAcc,
					"SK":skAcc,
					"NP":npAcc,
					"NK":nkAcc,
					"BK":bkAcc,
					"R8NewConfirmed": sumDaily,
					"R8Confirmed":accCases
		  }
	  })
 
  const r8PlotData = 
	  Object.keys(totalByDate).map((thisConfirmDate, key)=>{
		  return {"Date":thisConfirmDate.substr(0, 10).replace(/-/g,'/'), 
		  "R8NewConfirmed":totalByDate[thisConfirmDate].totalCount, 
		  "R8Confirmed":totalByDate[thisConfirmDate].accumulate}
	  })

  const allPlotData = 
	  Object.keys(totalR8ByDate).map((thisConfirmDate, key)=>{
		  return {"Date":thisConfirmDate.substr(0, 10).replace(/-/g,'/'), 
		  "R8NewConfirmed":0, "R8Confirmed":0}
	  })
  
  const outcome = getOutcome()
	
  return (
    <div className={classes.root}>
		<Grid container spacing={2}   direction="row"  justify="center" alignItems="center">
		<Grid item xs={12} sm={6}>
          <ThisLinePlot	
		    id='chart1'
			thisName={'Trend of Confirmed, Recovered, Hospitalized Cases'} 
			data={data} 
		    plotLine={{"Confirmed":"#8884d8","Hospitalized": "#50556C", "Recovered":"#82ca9d" }} 
			thisDot={false}
			avatarIcon={{' \u{1f631}':'Confirmed : จำนวนป่วยด้วยโรค COVID-19 สะสม บ่งชี้ถึงแนวโน้มการป่วย',  ' \u{1f917}':'Recovered : จำนวนหายจากโรค สะสม บ่งชี้ถึงผลลัพธ์การรักษา',' \u{1f6cf}':'Hospitalized : จำนวนรับไว้ในโรงพยาบาล สะสม บ่งชี้ถึงภาระของโรงพยาบาล'}}
		/>

		</Grid>
		<Grid item xs={12} sm={6}>
          <ThisLinePlot	
			  id='chart2'
    		  thisName={'Trend of NewConfirmed, NewRecovered, NewHospitalized Cases'} 
			  data={data} 
			  plotLine={{'NewConfirmed':'#8884d8','NewRecovered':'#82ca9d','NewHospitalized':'#50556C'}} 
			  avatarIcon={{' \u{1f631}':'NewConfirmed : จำนวนป่วยด้วยโรค COVID-19 รายใหม่วันนี้ บ่งชี้ถึงปัญหาปัจจุบัน', ' \u{1f917}':'NewRecovered : จำนวนหายจากโรควันนี้ บ่งชี้ถึงผู้เคยติดเชื้อได้กลับสู่ชุมชน ', ' \u{1f6cf}':'จำนวนรับไว้ในโรงพยาบาล วันนี้ บ่งชี้ภาระของโรงพยาบาลปัจจุบัน'}}
			  thisDot={false}
		  />
		</Grid>
		<Grid item xs={12} sm={6}>
          <ThisLinePlot
			  id='chart3'		  
			  thisName={'Trend of NewRecovered, NewHospitalized Cases'} 
			  data={data} 
			  plotLine={{'NewRecovered':'#82ca9d','NewHospitalized':'#50556C'}} 
			  avatarIcon={{' \u{1f917}':'NewRecovered : จำนวนหายจากโรควันนี้ บ่งชี้ถึงผู้เคยติดเชื้อได้กลับสู่ชุมชน ', ' \u{1f6cf}':'จำนวนรับไว้ในโรงพยาบาล วันนี้ บ่งชี้ภาระของโรงพยาบาลปัจจุบัน'}}
			  thisDot={false}
		  />
		</Grid>
		<Grid item xs={12} sm={6}>
          <ThisLinePlot
              id='chart4'		  
			  thisName={'Trend of NewConfirmed, NewRecovered'} 
			  data={data} 
			  plotLine={{'NewConfirmed':'#8884d8','NewRecovered':'#82ca9d'}} 
			  avatarIcon={{' \u{1f631}':'NewConfirmed : จำนวนป่วยด้วยโรค COVID-19 รายใหม่วันนี้ บ่งชี้ถึงปัญหาปัจจุบัน', ' \u{1f917}':'NewRecovered : จำนวนหายจากโรควันนี้ บ่งชี้ถึงผู้เคยติดเชื้อได้กลับสู่ชุมชน ' }}
			  thisDot={false}
		  />
		</Grid>
		<Grid item xs={12} sm={6}>
          <ThisLinePlot	
		    id='chart5'
			thisName={'Outcome from Mortality and Recovery Rate'} 
			data={outcome} 
		    plotLine={{"recoverRate": "#8884d8", 'deathRate':'#82ca9d'}} 
			thisDot={false}
			avatarIcon={{'\u{1f917}':'recoverRate: อัตราการหายป่วยจากโรค COVID-19', ' \u{1f480}':'deathRate: อัตราการเสียชีวิตจากโรค COVID-19'}}/>
		</Grid>
		<Grid item xs={12} sm={6}>
          <ThisLinePlot
			id='chart6'		  
			thisName={'Trend of Mortality'} 
			data={data} 
			plotLine={{'Deaths':'#8884d8', 'NewDeaths':'#82ca9d'}} 
			avatarIcon={{' \u{1f480}':'Deaths : จำนวนเสียชีวิตจากโรค COVID-19 สะสม', ' \u{1f47b}':'NewDeaths : จำนวนเสียชีวิตจากโรค COVID-19 วันนี้'}}
			thisDot={false}
			/>	
		</Grid>
		<Grid item xs={12} sm={6}>
          <ThisLinePlot
			id='chart7'		  
			thisName={'R8 Confirmed and NewConfirmed'} 
			data={totalR8ByDate} 
			plotLine={{'R8Confirmed':'#8884d8', 'R8NewConfirmed':'#82ca9d'}} 
			avatarIcon={{' \u{1f631}':'R8Confirmed : จำนวนป่วยด้วยโรค COVID-19 สะสมในเขตสุขภาพที่ 8 บ่งชี้ถึงแนวโน้มการป่วย', ' \u{1f631}':'R8NewConfirmed : จำนวนป่วยด้วยโรค COVID-19  รายใหม่ในเขตสุขภาพที่ 8 วันนี้ บ่งชี้ถึงปัญหาปัจจุบัน'}}
			thisDot={false}
			/>	
		</Grid>
		<Grid item xs={12} sm={6}>
          <ThisLinePlot
			id='chart7'		  
			thisName={'7 Provinces Confirmed Cases'} 
			data={totalR8ByDate} 
			plotLine={{'UT':'#8884d8', 'NB':'#82ca9d', 'LO':'#50556C', 'SK':'#ff7300', 'NP':'#676767', 'NK':'#556CD6', 'BK':'#444444'}}
			avatarIcon={{' \u{1f631}':'UT: จำนวนป่วยด้วยโรค COVID-19 สะสมในแต่ละจังหวัด บ่งชี้ถึงแนวโน้มการป่วย', ' \u{1f631}':'NB : จำนวนป่วยด้วยโรค COVID-19 สะสมในจังหวัดหนองบัวลำภู บ่งชี้ถึงแนวโน้มการป่วย'}}
			thisDot={false}
			/>	
		</Grid>
		<Grid item >
			<Typography variant="h6">covid19.th-stat.com Update at {updateOn}</Typography>		
	  </Grid>
	  </Grid>
	  
    </div>
  );
};

export default LinePlot
