import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import Tooltip from '@material-ui/core/Tooltip';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import {Box, Button, Grid, Avatar} from '@material-ui/core';
import { deepOrange, deepPurple, lightGreen, lightBlue } from '@material-ui/core/colors';
import AvatarGroup from '@material-ui/lab/AvatarGroup';
import Typography from '@material-ui/core/Typography';

import BrainIcon from 'img/BrainIcon.svg';
import LungIcon from 'img/LungIcon.svg';
import KidneysIcon from 'img/KidneysIcon.svg';
import HeartIcon from 'img/HeartIcon.svg';

const useStyles =  makeStyles(theme => ({
  root: {
	flexGrow: 1,
    height: '100%',
    maxWidth: 345
  },
  orange: {
    color: theme.palette.getContrastText(deepOrange[500]),
    backgroundColor: deepOrange[500],
  },
  purple: {
    color: theme.palette.getContrastText(deepPurple[500]),
    backgroundColor: deepPurple[500],
  },
  green: {
    color: theme.palette.getContrastText(deepOrange[100]),
    backgroundColor: deepOrange[100],
  },
  blue: {
    color: theme.palette.getContrastText(deepPurple[200]),
    backgroundColor: deepPurple[200],
  },
  media: {
    height: 5,
  },
    wrapIcon: {
    verticalAlign: 'middle',
    display: 'inline-flex'
   }
}));

const Home =() => {
  const classes = useStyles();
  return (
<React.Fragment>
	<Box mt={4} />
  	<Grid
			container
			justify='center'
			spacing={2}
			 alignItems="center"
	>
    <Card className={classes.root}>
      <CardActionArea>
        <CardMedia
          className={classes.media}
          title="Must Know COVID"
        />	
	<Tooltip title={'ปอดอักเสบ หรือปอดบวมจากเชื้อไวรัส พบบ่อย และเป็นเหตุเสียชีวิตมากที่สุด'} arrow>		
		<img 
			width='50%'
			src={LungIcon} 
			padding='50px'
			alt="Heart"
			/>
		</Tooltip>
	<Tooltip title={'โรคหัวใจอักเสบ จากเชื้อไวรัส หัวใจขาดเลือด  พบบ้าง '} arrow>		
		<img 
			width='25%'
			src={HeartIcon} 
			padding='20px'
			alt="Heart"
			/>
	</Tooltip> 
	<Tooltip title={'สมองอักเสบ จากเชื้อไวรัส พบน้อย'} arrow>		
		<img 
			width='15%'
			src={BrainIcon} 
			padding='15px'
			alt="Brain"
			/>
	</Tooltip>

        <CardContent>
		   <Typography
              color="textSecondary"
              gutterBottom
              variant="h3"
			  className={classes.wrapIcon}
            >
			COVID-19 &nbsp;&nbsp;&nbsp;&nbsp;
			<Tooltip title={'ไข้ ไอแห้ง ๆ จมูกไม่ได้กลิ่น ไม่รับรส ไม่สบายตัว อาการน้อย พบมากที่สุด'} arrow>		
				<Avatar >
					{' \u{1f631}'} 
				</Avatar>
		   </Tooltip>	
		   	<Tooltip title={'ไม่มีอาการใด ๆ แต่สามารถแพร่เชื้อได้ พบไม่น้อย'} arrow>		
				<Avatar >
					{' \u{1f924}'}
				</Avatar>
		   </Tooltip>	
          </Typography>
          <Typography variant="body2" color="textSecondary" component="p">
	<Tooltip title={' Corona Virus ติดต่อง่ายทางสารคัดหลั่งของร่างกาย ตามพื้นผิว น้ำมูก น้ำลาย ภายในระยะ 2 เมตร และผ่านละอองฝอยไอน้ำ ในระยะ 5 เมตร รวมทั้งทางอากาศ '}  arrow placement="top">		
		<AvatarGroup>
		  <Avatar className={classes.orange}>  C </Avatar> 
		  <Avatar className={classes.purple}>  O </Avatar> 
		  <Avatar className={classes.blue}>  V </Avatar> 
		  <Avatar className={classes.green}>  I </Avatar> 
		  <Avatar className={classes.green}>  D </Avatar> 		  
		</AvatarGroup>
	</Tooltip>
        Must Know COVID ช่วยให้ท่านเข้าใจสถานการณ์ปัจจุบัน แนวโน้ม โดยใช้ข้อมูลของประเทศไทยและประเทศต่าง ๆ รวมทั้งการป้องกันที่เหมาะสม
	<Tooltip title={'อยากได้ต้องป้องกัน อย่าเข้าสังคม อยู่ห่างกันอย่างน้อย 2 เมตร ดีที่สุด 5 เมตร (Social Distance)  และ หลีกเลี่ยงทุกคน อย่าเข้าไปในที่สาธารณะ เช่นห้องประชุม อย่าอยู่ในห้องเดียวกันหลายคน (Isolation)'} arrow placement="bottom" >		
		<AvatarGroup>
		  <Avatar className={classes.blue}>  G </Avatar> 
		  <Avatar>  U </Avatar> 
		  <Avatar className={classes.green}>  A </Avatar> 
		  <Avatar className={classes.orange}>  R </Avatar> 
		  <Avatar className={classes.purple}>  D </Avatar> 
		</AvatarGroup>
	</Tooltip>
			สำหรับประชาชน บุคลากรสุขภาพ ผู้บริหารเขตสุขภาพที่ 8
			แหล่งที่มาข้อมูล data.go.th update วันละหนึ่งครั้ง,  Johns Hopkins CSSE ทุก 15 นาที
		  </Typography>
        </CardContent>
      </CardActionArea>

    </Card>

	</Grid>
	</React.Fragment>
  );
}

export default Home