import React, {Fragment, useState} from 'react'
import { makeStyles } from '@material-ui/styles'
import { Grid, Card, CardContent, Typography, Avatar, Tooltip } from '@material-ui/core'
import * as d3 from "d3";
import axios from 'axios'
import GetCount from 'crud/GetCount'
import AdjustData from './AdjustData'
import {
  ThisLinePlot
} from './components';
import { useLocation} from "react-router";

const _ = require("lodash");

const useStyles = makeStyles(theme => ({
  root: {
    padding: theme.spacing(2)
  }
}));

const CompareThai = () => {
	let location = useLocation();

	const selectCountry=location.state.theCountry
	const classes = useStyles()
	
	const selectUrl= 'https://api.covid19api.com/dayone/country/'+selectCountry+'/status/confirmed'
	const recoverUrl= 'https://api.covid19api.com/dayone/country/'+selectCountry+'/status/recovered'
	const deathUrl= 'https://api.covid19api.com/dayone/country/'+selectCountry+'/status/deaths'
	const countryData = GetCount(selectUrl)	
	const confirmGetPlot = AdjustData(countryData,'Confirmed', 'NewConfirmed')	
	const recoverData = GetCount(recoverUrl)
	const recoverGetPlot = AdjustData(recoverData,'Recovered', 'NewRecovered')	
	const deathData = GetCount(deathUrl)
	const deathGetPlot = AdjustData(deathData, 'Deaths', 'NewDeaths')	

	const thConfirmUrl= 'https://api.covid19api.com/dayone/country/'+'thailand'+'/status/confirmed'
	const thRecoverUrl= 'https://api.covid19api.com/dayone/country/'+'thailand'+'/status/recovered'
	const thDeathUrl= 'https://api.covid19api.com/dayone/country/'+'thailand'+'/status/deaths'
	const thConfData = GetCount(thConfirmUrl)	
	const thConfirmGetPlot = AdjustData(thConfData,'Confirmed', 'NewConfirmed')	
	const thRecoverData = GetCount(thRecoverUrl)
	const thRecoverGetPlot = AdjustData(thRecoverData,'Recovered', 'NewRecovered')	
	const thDeathData = GetCount(thDeathUrl)
	const thDeathGetPlot = AdjustData(thDeathData, 'Deaths', 'NewDeaths')	

	
	const allDataTogether =confirmGetPlot?Object.keys(confirmGetPlot).map((caseConfirmDate, key)=>{
		const caseConfirm = confirmGetPlot[caseConfirmDate].Confirmed
		const caseNewConfirm = confirmGetPlot[caseConfirmDate].NewConfirmed
		const caseRecover = recoverGetPlot[key]?recoverGetPlot[key].Recovered:0
		const caseNewRecover = recoverGetPlot[key]?recoverGetPlot[key].NewRecovered:0
		const caseDeaths = deathGetPlot[key]?deathGetPlot[key].Deaths:0
		const caseNewDeaths = deathGetPlot[key]?deathGetPlot[key].NewDeaths:0
		const thCaseConfirm =  thConfirmGetPlot[caseConfirmDate]? thConfirmGetPlot[caseConfirmDate].Confirmed:0
		const thCaseNewConfirm = thConfirmGetPlot[caseConfirmDate]?thConfirmGetPlot[caseConfirmDate].NewConfirmed:0
		const thCaseRecover = thRecoverGetPlot[key]?thRecoverGetPlot[key].Recovered:0
		const thCaseNewRecover = thRecoverGetPlot[key]?thRecoverGetPlot[key].NewRecovered:0
		const thCaseDeaths = thDeathGetPlot[key]?thDeathGetPlot[key].Deaths:0
		const thCaseNewDeaths = thDeathGetPlot[key]?thDeathGetPlot[key].NewDeaths:0
		
		return  confirmGetPlot[caseConfirmDate].Date &&
			{"Date": confirmGetPlot[caseConfirmDate].Date, 
				[selectCountry.toUpperCase()+" Confirmed"]: caseConfirm, 
				[selectCountry.toUpperCase()+" NewConfirmed"]: caseNewConfirm,
				[selectCountry.toUpperCase()+" Recovered"]: caseRecover,
				[selectCountry.toUpperCase()+" NewRecovered"]: caseNewRecover,
				[selectCountry.toUpperCase()+" Deaths"]: caseDeaths,
				[selectCountry.toUpperCase()+" NewDeaths"]: caseNewDeaths, 
				[selectCountry.toUpperCase()+" recoverRate"]: caseRecover*100/(caseRecover+caseDeaths),
				[selectCountry.toUpperCase()+" deathRate"]: caseDeaths*100/(caseDeaths+caseRecover),
				
				"Thai Confirmed": thCaseConfirm, 
				"Thai NewConfirmed": thCaseNewConfirm,
				"Thai Recovered": thCaseRecover,
				"Thai NewRecovered": thCaseNewRecover,
				"Thai Deaths": thCaseDeaths,
				"Thai NewDeaths": thCaseNewDeaths, 
				"Thai recoverRate": thCaseRecover*100/(thCaseRecover+thCaseDeaths),
				"Thai deathRate": thCaseDeaths*100/(thCaseDeaths+thCaseRecover)
			}
	}):[]
	
	return (
    <div className={classes.root}>
		<Grid container spacing={2}   direction="row"  justify="center" alignItems="center">
		<Grid item xs={12} sm={6}>
          <ThisLinePlot	
		    id='chart1'
			thisName={selectCountry.toUpperCase()+'- Thailand  Trend of Confirmed, NewConfirmed Cases'} 
			data={allDataTogether} 
		    plotLine={{[selectCountry.toUpperCase()+" Confirmed"]:"#8884d8","Thai Confirmed": "#82ca9d"}} 
			thisDot={false}
			avatarIcon={{' \u{1f631}':'Confirmed : จำนวนป่วยด้วยโรค COVID-19 สะสม บ่งชี้ถึงแนวโน้มการป่วย',' \u{1f917}':'Thai Confirmed : จำนวนป่วยด้วยโรค COVID-19 สะสม บ่งชี้ถึงแนวโน้มการป่วย'}}
		/>
		</Grid>
		<Grid item xs={12} sm={6}>
          <ThisLinePlot	
		    id='chart2'
			thisName={selectCountry.toUpperCase()+'- Thailand New Confirmed Cases  '} 
			data={allDataTogether} 
		    plotLine={{[selectCountry.toUpperCase()+' NewConfirmed']:'#8884d8','Thai NewConfirmed':'#82ca9d'}} 
			avatarIcon={{' \u{1f631}':'NewConfirmed : จำนวนป่วยด้วยโรค COVID-19 รายใหม่วันนี้ บ่งชี้ถึงปัญหาปัจจุบัน'}}
			  thisDot={false}
		/>
		</Grid>
		<Grid item xs={12} sm={6}>
          <ThisLinePlot	
		    id='chart3'
			thisName={selectCountry.toUpperCase()+'- Thailand  Trend of Recovered, New Recovered Cases  '} 
			data={allDataTogether} 
		    plotLine={{[selectCountry.toUpperCase()+" Recovered"]:"#8884d8",[selectCountry.toUpperCase()+" NewRecovered"]: "#82ca9d", "Thai Recovered":"#556CD6",  'Thai NewRecovered':'#ff7300' }}
			thisDot={false}
			avatarIcon={{ ' \u{1f917}':'Recovered : จำนวนหายจากโรค สะสม บ่งชี้ถึงผลลัพธ์การรักษา',  ' \u{1f917}':'NewRecovered : จำนวนหายจากโรควันนี้ บ่งชี้ถึงผู้เคยติดเชื้อได้กลับสู่ชุมชน '}}
		/>
		</Grid>
		<Grid item xs={12} sm={6}>
          <ThisLinePlot	
		    id='chart4'
			thisName={selectCountry.toUpperCase()+'  Trend of Deaths, New Deaths Cases  '} 
			data={allDataTogether} 
		    plotLine={{[selectCountry.toUpperCase()+" Deaths"]:"#8884d8",[selectCountry.toUpperCase()+" NewDeaths"]: "#82ca9d",  "Thai Deaths":"#556CD6",  'Thai NewDeaths':'#444444' }} 
			thisDot={false}
			avatarIcon={{' \u{1f480}':'Deaths : จำนวนเสียชีวิตจากโรค COVID-19 สะสม', ' \u{1f47b}':'NewDeaths : จำนวนเสียชีวิตจากโรค COVID-19 วันนี้'}}
		/>
		</Grid>
		<Grid item xs={12} sm={6}>
		<ThisLinePlot	
		    id='chart5'
			thisName={selectCountry.toUpperCase()+'Outcome from Mortality and Recovery Rate'} 
			data={allDataTogether} 
		    plotLine={{[selectCountry.toUpperCase()+" recoverRate"]: "#8884d8", [selectCountry.toUpperCase()+' deathRate']:'#82ca9d'}} 
			thisDot={false}
			avatarIcon={{'\u{1f917}':'recoverRate: อัตราการหายป่วยจากโรค COVID-19', ' \u{1f480}':'deathRate: อัตราการเสียชีวิตจากโรค COVID-19'}}/>
		</Grid>
		<Grid item xs={12} sm={6}>
		<ThisLinePlot	
		    id='chart6'
			thisName={selectCountry.toUpperCase()+' Thailand Outcome from Mortality and Recovery Rate'} 
			data={allDataTogether} 
		    plotLine={{"Thai recoverRate": "#8884d8", 'Thai deathRate':'#82ca9d'}} 
			thisDot={false}
			avatarIcon={{'\u{1f917}':'recoverRate: อัตราการหายป่วยจากโรค COVID-19', ' \u{1f480}':'deathRate: อัตราการเสียชีวิตจากโรค COVID-19'}}/>
		</Grid>
		</Grid>
	</div>
	)
	
}

export default CompareThai